    @import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;600&display=swap");
    
    :root {
      --red: #fa4f09;
      --black: #333;
      --white: #fff;
      --light-color: #666;
      --light-bg: #eee;
    }
    
    * {
      font-family: 'Poppins', sans-serif;
      margin: 0;
      padding: 0;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      outline: none;
      border: none;
      text-decoration: none;
      text-transform: capitalize;
      -webkit-transition: all .2s linear;
      transition: all .2s linear;
    }
    
    html {
      font-size: 62.5%;
      overflow-x: hidden;
      scroll-behavior: smooth;
      scroll-padding-top: 8rem;
    }
    
    html::-webkit-scrollbar {
      width: 1rem;
    }
    
    html::-webkit-scrollbar-track {
      background: transparent;
    }
    
    html::-webkit-scrollbar-thumb {
      background: var(--red);
      border-radius: 5rem;
    }
    
    body {
      background: var(--white);
    text-align: center;
    justify-content: center;
      background-color: #fa4f09 #676766 #999  #236768;
    }
    
    body.active {
      --black: #fff;
      --white: #111;
      --light-color: #aaa;
      --light-bg: #222;
    }
    
    section {
      padding: 3rem 9%;
    }
    
    .btn {
      display: inline-block;
      margin-top: 1rem;
      border-radius: .5rem;
      background: var(--light-bg);
      cursor: pointer;
      color: var(--black);
      padding: 1rem 3rem;
      font-size: 1.7rem;
    }
    
    .btn:hover {
      background: var(--red);
      color: #fff;
      scroll-behavior: initial;
    }
    
    .heading {
      text-align: center;
      margin-bottom: 3rem;
      font-size: 4rem;
      color: var(--black);
    }
    
    .heading span {
      color: var(--red);
    }
    
    .header {
      position: -webkit-sticky;
      position: sticky;
      top: 0;
      left: 0;
      right: 0;
      align-items: center;
      z-index: 1000;
      text-align: center;
      background: var(--light-bg);
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      padding: 2rem 9%;
    }
    
    .header .logo {
      margin-right: auto;
      color: var(red);
      font-size: 2.5rem;
    }
    
    .header .logo i {
      color: var(red);
      background-color: red;
    }
    
    .header .navbar a {
     
      margin-right: 13rem;;
      font-size: 1.4rem;
     
      
      
      color: var(--light-color);
    }
    
    .header .navbar a:hover {
      color: var(--red);
    scroll-behavior: smooth;
      scroll-snap-type: proximity;
      scroll-snap-type: mandatory;
      transition: 1s;
    }
    
    
    .navbar a{
      text-align: center;
      margin: auto;
    }
    
    .header #menu-btn,
    .header #theme-btn {
      font-size: 2.5rem;
      margin-left: 2rem;
      cursor: pointer;
      color: var(--black);
    }
    
    .header #menu-btn:hover,
    .header #theme-btn:hover {
      color: var(--red);
    }
    
    .header #menu-btn {
      display: none;
    }
    
    .header #theme-btn.fa-sun {
      -webkit-transform: rotate(90deg);
      transform: rotate(90deg);
    }
    
    .header .scroll-indicator {
      position: absolute;
      top: 99%;
      left: 0;
      height: 1rem;
      background: var(--red);
      text-align: center;
    }
    
    .home .box {
      min-height: 70vh;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      -ms-flex-wrap: wrap;
      flex-wrap: wrap;
      gap: 2rem;
    }
    
    
    
    
    .logo  img{
    
    
      width: 80px;
    
      
    }

    .logo:hover{
      color: blueviolet;
  background-color: #40e2e5;
      
      scroll-behavior: smooth;
    
      transform: translate(360deg);
      z-index: 1;
      opacity: 0;
      transition: 1s;
     
      
    }
    
    
    
    .homes{
      color: #fa4f09;
      
